
@import 'variables';

* {
  padding: $none-value;
  margin: $none-value;
}

body {
  margin: $none-value;
  font-family: $basic-font;
  background: #F5F6FA;
}
h2 {
  font-weight: 500;
  font-size: 24px;
  line-height: normal;
  margin: 0 0 15px;
  display: $flex;
  align-items: $center;
  justify-content: flex-start;
  color: $basic-color;
}

h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: normal;
  margin: 0 0 15px;
  display: $flex;
  align-items: $center;
  justify-content: flex-start;
  color: $basic-color;
}

h6 {
  font-weight: 500;
  font-size: 16px;
  line-height: normal;
}

a {
  color: $primary-color;
  text-decoration: none;
  &:hover {
    text-decoration: none;
    color: $primary-color;
  }
}
.clientshare-v2-container {
  padding-top: 56px;
}
.v2-navbar.navbar {
  display: $flex;
  min-height: $full-width;
  padding: 56px 0 0 0;
  align-items: flex-start;
  position: fixed;
  .navigation-col {
    width: auto;
    display: $flex;
    background: $basic-color;
    flex-direction: column;
    height: calc(100vh - 56px);
    justify-content: space-between;
    .navbar-nav {
      li {
        list-style: none;
        width: 56px;
        height: 60px;
        &:last-child {
          a {
            border-bottom: none;
          }
        }
        a {
          padding: 18px 13px;
          display: $flex;
          height: 100%;
          justify-content: $center;
          border-bottom: 1px solid rgba($black-color, .4);
          position: relative;
          -webkit-transform: perspective(1px) translateZ(0);
          transform: perspective(1px) translateZ(0);
          &::before {
            content: '';
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba($black-color, .4);
            -webkit-transform: scaleX(0);
            transform: scaleX(0);
            -webkit-transform-origin: 0 50%;
            transform-origin: 0 50%;
            -webkit-transition-property: transform;
            transition-property: transform;
            -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
            -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
          }
          &:hover {
            &::before {
              -webkit-transform: scaleX(1);
              transform: scaleX(1);
            }
          }
        }
      }
      .active {
        a {
          background: $primary-color;
          border-bottom: none;
          &::before {
            background: transparent;
          }
        }
        position: relative;
        &::before {
          content: '';
          content: "";
          position: absolute;
          top: 0;
          left: 100%;
          right: 0;
          bottom: 0;
          background: $primary-color;
          border-top-right-radius: 8px;
          border-bottom-right-radius: 8px;
          width: 8px;
        }
      }
    }
    .bottom-side-nav {
      li {
        a {
        justify-content: $center;
        }
      }
    }
  }
}

.top-navbar {
  min-height: auto;
  height: 56px;
  background: $white-color;
  display: $flex;
  position: fixed;
  width: 100%;
  z-index: 9;
  &::before {
    background: rgba($basic-color, .5);
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    height: 56px;
}
  .share-dropdown-wrap {
    display: $flex;
    min-height: 100%;
    justify-content: $center;
    flex-direction: column;
    width: 50%;
    z-index: 9;
    border-right: 1px solid $white-color;
      .navbar-nav{
        list-style: none;
        li{
          display: $flex;
          padding-left: 10px;
          align-items: $center;
          position: relative;
          min-height: 100%;
          span.share-logo {
            width: 36px;
            height: 36px;
            margin-right: 5px;
            border-radius: 50%;
            background: $white-color;
            display: block;
            img {
              max-width: 100%;
              border-radius: 50%;
              min-height: 100%;
              object-fit: cover;
              object-position: $center;
            }
          }
          a.nav-link{
            font-style: normal;
            font-weight: 500;
            font-size: 18px;
            margin-left: 10px;
            line-height: normal;
            color: $white-color;
            display: $flex;
            align-items: $center;
            justify-content: $center;
            position: relative;
          }
          .dropdown-menu {
            width: 412px;
            min-width: auto;
            border-radius: 0 0 10px 0;
            overflow: hidden;
            .dropdown-item {
                margin: 0;
                border-bottom: 1px solid rgba($black-color, .4);
                padding: 0;
                a{
                  padding: 18px 16px;
                  font-weight: 500;
                  font-size: 18px;
                  display: $flex;
                  justify-content: space-between;
                  width: 100%;
                  color: $white-color;
                  white-space: normal;
                  &:hover, &:focus{
                    text-decoration: none;
                    outline: none;
                  }
                }
                &:last-child {
                    border-bottom: none;
                    &:hover {
                        border-radius: 0 0 10px 0;
                    }
                }
            }
          }
        }
      }
    }
    .profile-wrap {
      display: flex;
      min-height: 100%;
      justify-content: space-between;
      min-width: 256px;
      background: $basic-color;
      border-radius: 8px 0 0 8px;
      .notification-wrap {
        display: flex;
        width: 56px;
        background-color: rgba($black-color, .4);
        border-radius: 8px 0 0 8px;
        min-height: 100%;
        align-items: $center;
        justify-content: $center;
    }
      .navbar-nav{
        list-style: none;
        width: calc(100% - 56px);
        display: $flex;
        align-items: flex-start;
        justify-content: $center;
        li{
            display: $flex;
            align-items: $center;
            flex-direction: row;
            padding: 0 20px 0 16px;
            justify-content: flex-start;
            padding-left: 16px;
            width: 100%;
            img {
                border: 2px solid $primary-color;
                border-radius: 50px;
              }
            a.nav-link{
                color: $white-color;
                margin-left: 10px;
                position: relative;
                min-height: 100%;
                align-items: $center;
                display: $flex;
                span{
                    color: $primary-color;
                    margin-left: 4px;
                }
                &::after {
                    width: 9px;
                    height: 9px;
                    background: url('/../images/v2-images/down-arrow-green.png') no-repeat;
                  }
            }
            &:hover{
                color: #fff;
            }
            .dropdown-menu {
                width: 122px;
                min-width: auto;
                right: 0;
                left: auto !important;
                border-radius: 0 0 0 10px;
                .dropdown-item {
                  margin: 0;
                  border-bottom: 1px solid rgba($black-color, .4);
                  padding: 0;
                  a{
                    padding: 15px 18px;
                    font-weight: 400;
                    font-size: 16px;
                    margin: 0;
                    width: 100%;
                    color: $white-color;
                    white-space: normal;
                    &:hover, &:focus{
                      text-decoration: none;
                      outline: none;
                    }
                  }
                  &:last-child {
                      border-bottom: none;
                      &:hover {
                        border-radius: 0 0 0 10px;
                      }
                  }
              }
              }
        }
      }
    }
    .dropdown-menu {
        background: #242f47;
        -webkit-transform: none !important;
        transform: none !important;
        top: 100% !important;
        padding: 0;
        margin: 0;
        border: none;
        .dropdown-item {
            text-align: left;
            color: $white-color;
            border-bottom: 1px solid rgba($black-color, .4);
            line-height: normal;
            justify-content: flex-start;
            &:hover, &:focus, &:active, &.active{
                background:  rgba($black-color, .4);
                outline: none;
            }
        }
    }
    .dropdown-toggle::after {
        border: none;
        width: 12px;
        height: 12px; 
        margin: 0 0 0 10px;
        vertical-align: middle;
        background: url('/../images/v2-images/down-arrow.png') no-repeat;
      }
}

.custom-scrollbar {
  max-height: 345px;
  overflow-y: scroll;
}

::-webkit-scrollbar {
width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
background: rgba($black-color, .4);
border-radius: 50px; 
}

/* Handle */
::-webkit-scrollbar-thumb {
background: $black-color; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background: $black-color; 
}

input:-internal-autofill-previewed, 
input:-internal-autofill-selected, 
textarea:-internal-autofill-previewed, 
textarea:-internal-autofill-selected, 
select:-internal-autofill-previewed, 
select:-internal-autofill-selected {
background-color: rgba($black-color, .4);
background-image: none;
color: $white-color !important;
}

.notification-count {
    background: $alert-color;
    width: 24px;
    height: 24px;
    text-align: center;
    font-weight: 500;
    font-size: 14px;
    line-height: normal;
    border-radius: 50px;
    display: flex;
    color: $white-color;
    align-items: center;
    justify-content: center;
}

.search-box {
  font-size: 14px;
  background: url('/../images/v2-images/search-icon.png') center left no-repeat;
  line-height: 22px;
  border: none;
  color: $white-color;
  &:focus {
      border: none;
      outline: none;
      box-shadow: none;
      color: $white-color;
    }
  }

.share-search {
  .search-box {
    background-color: rgba($black-color, .4);
    padding: 18px 40px;
    background-position: 16px 50%;
    &:focus {
        background-color: rgba($black-color, .4);
      }
    }
}

.header-search-wrap {
  min-height: $full-width;
  width: 32%;
  padding: 0 16px;
  position: relative;
  flex: 1;
  form {
    min-height: 100%;
    .search-box {
      height: 100%;
      background-color: transparent;
      padding: 16px 22px;
      &:focus {
        background-color: transparent;
      }
    }
  }
  .search-dropdown-wrap {
    &::before {
      border-bottom: 10px solid $white-color;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      content: "";
      height: 0;
      left: 0;
      margin: 4px auto;
      position: absolute;
      right: 0;
      width: 0;
      z-index: 9999;
    }
    .search-dropdown {
      list-style: none;
      padding: $none-value;
      position: absolute;
      z-index: 999999;
      background: $white-color;
      border: 1px solid $dark-white;
      box-shadow: $shadow;
      border-radius: 10px;
      li {
        a {
          color: $basic-color;
          display: inline-block;
          border-bottom: 1px solid $dark-white;
          width: $full-width;
          padding: 13px 16px 11px;
          &:hover {
            background: #F5F6FA;
          }
          h6 {
            margin: 0 0 8px;
          }
          p {
            color: $light-gray;
            font-size: $normal-font;
            margin: $none-value;
          }
        }
      }
    }
  }
}


input[type="search"]::placeholder { /* Firefox, Chrome, Opera */ 
  color: $white-color;
} 

input[type="search"]:-ms-input-placeholder { /* Internet Explorer 10-11 */ 
  color: $white-color;
} 

input[type="search"]::-ms-input-placeholder { /* Microsoft Edge */ 
  color: $white-color;
} 

input[type=text]::-ms-clear {  display: none; width : 0; height: 0; }
input[type=text]::-ms-reveal {  display: none; width : 0; height: 0; }
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { display: none; }

.container-bg {
  background: $white-color;
  box-shadow: 8px 8px 14px rgba(190, 197, 214, 0.2), -8px -8px 14px rgba(190, 197, 214, 0.2);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 100%;
  padding: 15px;
}

.box-equal {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.invite-col {
  max-width: 299px;
  position: fixed;
  bottom: 0;
  left: 72px;
}

.community-head p {
  font-size: 10px;
  line-height: 13px;
  color: $light-gray;
  margin: 13px 0 0;
}

.btn {
  font-weight: 500;
  font-size: $normal-font;
  background: $primary-color;
  border-radius: $normal-font - 8px;
  border: none;
  color: $white-color;
  padding: 12px 18.8px;
  outline: none;
  cursor: pointer;
}

.user-profile-row {
  width: 100%;
  display: $flex;
  margin: 14.86px 0 0;
  justify-content: space-between;
}

.user-profile-row span {
  width: 30.33px;
  height: 30.33px;
  display: $flex;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
}

.link-text {
  color: $primary-color;
  font-weight: 500;
  font-size: $normal-font;
  margin: 13px 0 0;
}


.main-container {
  display: flex;
  padding: 0 35px 0 60px;
}

.categories-wrap {
  display: flex;
  width: 30%;
  padding-top: 16px;
  flex-direction: column;
  .category-container {
    background: #EDEFF5;
    border-radius: 10px;
    padding: 16px 18px;
    width: 100%;
    max-width: 299px;
    margin-bottom: 12px;
    position: relative;
    .category-name {
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: normal;
      margin: 0 0 15px 0;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      color: #293248;
      img {
        margin-right: 8px;
    }
    }
    .last-updated {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: normal;
      color: #293248;
      margin: 0 0 2px 0;
      span{
        color: #2CCDA0;
      }
    }
    p {
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 22px;
      color: #748AA1;
      margin: 0;
    }
    .notification-count{
      position: absolute;
      right: 7px;
      top: 7px;
    }
  }
}

.user-profile-pic {
  width: 40px;
  height: 40px;
  display: block;
  background-size: cover;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid #2CCDA0;
}

.modal-dialog {
  max-width: 478px;
  .modal-content {
    background: #fff;
    border-radius: 10px;
    border: none;
  }
  .modal-header {
    background: #F5F6FA;
    border-bottom: 1px solid #E8F0F8;
    box-sizing: border-box;
    padding: 15px 20px;
    .modal-title {
      font-weight: 500;
      font-size: 18px;
      line-height: normal;
      color: #293248;
      margin: 0;
  }
  .close {
    opacity: 1;
    padding: 0;
    position: absolute;
    right: 20px;
    margin: 0;
    top: 15px;
  }
}

.modal-body {
  padding: 15px 20px;
  border: 0px;
}
}

.profile-edit-wrap {
  display: flex;
  .profile-image-wrap {
    position: relative;
    span {
      width: 175px;
      height: 175px;
      display: block;
      border-radius: 50%;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
    }
    .uploaded-profile-pic {
      position: absolute;
      top: 0;
      left: 0;
      .upload-pic-icon {
        width: 40px;
        height: 40px;
        background: #2CCDA0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 6px;
        bottom: 4px;
      }
    }
    .file-input {
      width: 175px;
      height: 175px;
      position: absolute;
      right: 0;
      bottom: 0;
      border-radius: 50%;
      cursor: pointer;
      opacity: 0;
      z-index: 9;
    }
  }
  .profile-name-wrap {
    width: 100%;
    padding: 10px 0px 0px 15px;
}
}

